nav {
    background-color: black;
    display: flex;
    justify-content: space-between;
    padding: 20px 20px;
    color: #FFFFFF; /* White text color */
    font-family: 'Montserrat', sans-serif;
    font-family: 'Roboto Condensed', sans-serif;
  }
  
  .nav-left button {
    font-family: 'Roboto Condensed', sans-serif;
    background: none;
    border: none;
    color: #FFFFFF; /* White text color for the button */
    font-weight: bold;
    cursor: pointer;
    font-size: large;
  }
  
  .nav-right a {
    color: #FFFFFF; /* White text color for links */
    text-decoration: none;
    margin-left: 20px;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: large;
    font-weight: bold;
  }
  
  .nav-right a:first-child {
    margin-left: 0; /* Remove margin for the first link to avoid space on the left */
  }

  /* Additional styles for social media icons */
.nav-right img {
  width: 24px; /* Adjust the icon size as needed */
  height: 24px;
  margin-left: 10px; /* Add spacing between the icons */
}